/**
 * Message input component for composing and sending encrypted messages
 */
import React, { useState } from "react";
import { encryptMessage, arrayBufferToBase64 } from "../../lib/crypto";

interface MessageInputProps {
  encryptionKey: CryptoKey;
  onSend: (message: {
    id: string;
    content: string;
    timestamp: number;
    expiresIn: number;
  }) => void;
}

const MessageInput: React.FC<MessageInputProps> = ({ encryptionKey, onSend }) => {
  const [text, setText] = useState("");
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!text.trim()) {
      setError("Cannot send an empty message.");
      return;
    }

    if (!encryptionKey) {
      setError("Encryption not ready. Please wait.");
      return;
    }

    setError(null);
    setIsLoading(true);

    try {
      // Encrypt the message text
      const { cipherText, iv } = await encryptMessage(text.trim(), encryptionKey);
      
      // Convert to base64 for storage
      const encryptedContent = arrayBufferToBase64(cipherText);
      const ivBase64 = arrayBufferToBase64(iv.buffer);
      
      // Create message object with encrypted content
      const message = {
        id: crypto.randomUUID(),
        content: `${encryptedContent}:${ivBase64}`, // Store cipher:iv
        timestamp: Date.now(),
        expiresIn: 30000, // 30 seconds auto-delete
      };

      onSend(message);
      setText("");
    } catch (err) {
      console.error("Message encryption failed:", err);
      setError("Failed to encrypt message. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  return (
    <div className="border-t border-border bg-background p-4">
      <form onSubmit={handleSubmit} className="space-y-2">
        <div className="flex gap-2">
          <textarea
            placeholder="Type your encrypted message..."
            className="flex-1 min-h-[40px] max-h-[120px] p-3 border border-input rounded-lg bg-background text-foreground placeholder:text-muted-foreground resize-none focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent"
            value={text}
            onChange={(e) => setText(e.target.value)}
            onKeyPress={handleKeyPress}
            disabled={isLoading}
            rows={1}
          />
          <button
            type="submit"
            disabled={isLoading || !text.trim() || !encryptionKey}
            className="px-6 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
          >
            {isLoading ? "..." : "Send"}
          </button>
        </div>
        
        {error && (
          <div className="text-sm text-destructive bg-destructive/10 p-2 rounded border border-destructive/20">
            {error}
          </div>
        )}
        
        <div className="text-xs text-muted-foreground">
          Messages auto-delete in 30 seconds ‚Ä¢ End-to-end encrypted
        </div>
      </form>
    </div>
  );
};

export default MessageInput;
