/**
 * Custom hook for auto-deleting messages after a specified duration
 */
import { useEffect, useState } from "react";

export function useAutoDelete(
  messageId: string, 
  duration: number, 
  onDelete: (id: string) => void
) {
  useEffect(() => {
    if (duration <= 0) return;

    const timer = setTimeout(() => {
      try {
        onDelete(messageId);
      } catch (err) {
        console.error("Auto-delete error for message:", messageId, err);
      }
    }, duration);

    return () => clearTimeout(timer);
  }, [messageId, duration, onDelete]);
}

export function useCountdown(initialTime: number, onComplete?: () => void) {
  const [timeLeft, setTimeLeft] = useState(initialTime);

  useEffect(() => {
    if (timeLeft <= 0) {
      onComplete?.();
      return;
    }

    const timer = setInterval(() => {
      setTimeLeft((prev: number) => {
        const newTime = prev - 1000;
        if (newTime <= 0) {
          onComplete?.();
          return 0;
        }
        return newTime;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [timeLeft, onComplete]);

  return timeLeft;
}
