/**
 * Main GhostChat application page
 * Integrates all components and manages encryption, state, and stealth mode
 */
"use client";
import React, { useEffect, useState, useCallback } from "react";
import ChatWindow from "../components/Chat/ChatWindow";
import MessageInput from "../components/Chat/MessageInput";
import StealthCalculator from "../components/Chat/StealthCalculator";
import { generateKey, decryptMessage, base64ToArrayBuffer } from "../lib/crypto";

interface Message {
  id: string;
  content: string;
  timestamp: number;
  expiresIn: number;
}

interface EncryptedMessage {
  id: string;
  encryptedContent: string;
  iv: string;
  timestamp: number;
  expiresIn: number;
}

const GhostChatPage: React.FC = () => {
  const [encryptionKey, setEncryptionKey] = useState<CryptoKey | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [encryptedMessages, setEncryptedMessages] = useState<EncryptedMessage[]>([]);
  const [stealthMode, setStealthMode] = useState<boolean>(false);
  const [isInitializing, setIsInitializing] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Initialize encryption key on mount
  useEffect(() => {
    const initializeEncryption = async () => {
      try {
        setError(null);
        const key = await generateKey();
        setEncryptionKey(key);
      } catch (err) {
        console.error("Failed to generate encryption key:", err);
        setError("Failed to initialize encryption. Please refresh the page.");
      } finally {
        setIsInitializing(false);
      }
    };

    initializeEncryption();
  }, []);

  // Decrypt messages when encryption key is available
  useEffect(() => {
    if (!encryptionKey || encryptedMessages.length === 0) {
      setMessages([]);
      return;
    }

    const decryptMessages = async () => {
      const decryptedMessages: Message[] = [];
      
      for (const encMsg of encryptedMessages) {
        try {
          const [encryptedContent, ivBase64] = encMsg.encryptedContent.split(':');
          const cipherText = base64ToArrayBuffer(encryptedContent);
          const iv = new Uint8Array(base64ToArrayBuffer(ivBase64));
          
          const decryptedContent = await decryptMessage(cipherText, encryptionKey, iv);
          
          decryptedMessages.push({
            id: encMsg.id,
            content: decryptedContent,
            timestamp: encMsg.timestamp,
            expiresIn: Math.max(0, encMsg.expiresIn - (Date.now() - encMsg.timestamp)),
          });
        } catch (err) {
          console.error("Failed to decrypt message:", encMsg.id, err);
          // Skip corrupted messages
        }
      }
      
      setMessages(decryptedMessages);
    };

    decryptMessages();
  }, [encryptionKey, encryptedMessages]);

  const handleSendMessage = useCallback((encryptedMsg: {
    id: string;
    content: string;
    timestamp: number;
    expiresIn: number;
  }) => {
    const [encryptedContent, ivBase64] = encryptedMsg.content.split(':');
    
    const newEncryptedMessage: EncryptedMessage = {
      id: encryptedMsg.id,
      encryptedContent: encryptedMsg.content,
      iv: ivBase64,
      timestamp: encryptedMsg.timestamp,
      expiresIn: encryptedMsg.expiresIn,
    };

    setEncryptedMessages(prev => [...prev, newEncryptedMessage]);
  }, []);

  const handleDeleteMessage = useCallback((id: string) => {
    setEncryptedMessages(prev => prev.filter(msg => msg.id !== id));
    setMessages(prev => prev.filter(msg => msg.id !== id));
  }, []);

  const handlePanic = useCallback(() => {
    // Immediately wipe all messages and clear any local storage
    setMessages([]);
    setEncryptedMessages([]);
    
    try {
      // Clear any potential local storage
      localStorage.removeItem("ghostChatMessages");
      localStorage.removeItem("ghostChatKey");
      sessionStorage.clear();
    } catch (e) {
      console.error("Storage clearance failed:", e);
    }

    // Show confirmation
    setTimeout(() => {
      alert("üö® PANIC MODE ACTIVATED: All messages have been permanently deleted.");
    }, 100);
  }, []);

  const handleClearAll = useCallback(() => {
    if (messages.length === 0) return;
    
    if (window.confirm("Clear all messages? This action cannot be undone.")) {
      setMessages([]);
      setEncryptedMessages([]);
    }
  }, [messages.length]);

  const toggleStealthMode = useCallback(() => {
    setStealthMode(prev => !prev);
  }, []);

  // Prevent screenshots and screen recording (limited browser support)
  useEffect(() => {
    const preventScreenshot = (e: KeyboardEvent) => {
      // Prevent common screenshot shortcuts
      if (
        (e.ctrlKey && e.shiftKey && (e.key === 'S' || e.key === 's')) ||
        (e.metaKey && e.shiftKey && (e.key === '3' || e.key === '4' || e.key === '5')) ||
        e.key === 'PrintScreen'
      ) {
        e.preventDefault();
        alert("Screenshots are disabled for security.");
        return false;
      }
    };

    const preventRightClick = (e: MouseEvent) => {
      e.preventDefault();
      return false;
    };

    document.addEventListener('keydown', preventScreenshot);
    document.addEventListener('contextmenu', preventRightClick);

    return () => {
      document.removeEventListener('keydown', preventScreenshot);
      document.removeEventListener('contextmenu', preventRightClick);
    };
  }, []);

  // Loading state
  if (isInitializing) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center space-y-4">
          <div className="w-12 h-12 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto"></div>
          <div>
            <h2 className="text-lg font-medium text-foreground">Initializing GhostChat</h2>
            <p className="text-sm text-muted-foreground">Setting up end-to-end encryption...</p>
          </div>
        </div>
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center space-y-4 max-w-md">
          <div className="text-4xl">‚ö†Ô∏è</div>
          <div>
            <h2 className="text-lg font-medium text-foreground">Encryption Error</h2>
            <p className="text-sm text-muted-foreground mt-2">{error}</p>
          </div>
          <button
            onClick={() => window.location.reload()}
            className="px-4 py-2 bg-primary text-primary-foreground rounded hover:bg-primary/90 transition-colors"
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  // Stealth mode
  if (stealthMode) {
    return <StealthCalculator onExit={toggleStealthMode} />;
  }

  // Main chat interface
  return (
    <div className="min-h-screen flex flex-col bg-background">
      <div className="flex-1 flex flex-col max-w-4xl mx-auto w-full">
        <ChatWindow
          messages={messages}
          onPanic={handlePanic}
          onDeleteMessage={handleDeleteMessage}
          onToggleStealth={toggleStealthMode}
          onClearAll={handleClearAll}
        />
        
        {encryptionKey && (
          <MessageInput 
            encryptionKey={encryptionKey} 
            onSend={handleSendMessage} 
          />
        )}
      </div>
      
      {/* Security notice */}
      <div className="text-xs text-center text-muted-foreground p-2 border-t border-border">
        üîí GhostChat ‚Ä¢ End-to-end encrypted ‚Ä¢ Auto-deleting messages ‚Ä¢ No data stored on servers
      </div>
    </div>
  );
};

export default GhostChatPage;
