/**
 * Individual message component with auto-deletion countdown
 */
import React, { useState, useEffect } from "react";
import { useAutoDelete } from "../../hooks/useAutoDelete";

interface Message {
  id: string;
  content: string;
  timestamp: number;
  expiresIn: number; // ms remaining for auto-deletion
}

interface MessageItemProps {
  message: Message;
  onDelete: (id: string) => void;
}

const MessageItem: React.FC<MessageItemProps> = ({ message, onDelete }) => {
  const [timeLeft, setTimeLeft] = useState(message.expiresIn);

  // Start auto-delete timer when component mounts
  useAutoDelete(message.id, message.expiresIn, onDelete);

  // Update countdown display
  useEffect(() => {
    if (timeLeft <= 0) return;

    const timer = setInterval(() => {
      setTimeLeft((prev) => Math.max(0, prev - 1000));
    }, 1000);

    return () => clearInterval(timer);
  }, [timeLeft]);

  const formatTime = (ms: number) => {
    const seconds = Math.ceil(ms / 1000);
    return `${seconds}s`;
  };

  const getTimeColor = (ms: number) => {
    const seconds = ms / 1000;
    if (seconds <= 3) return "text-red-500";
    if (seconds <= 10) return "text-yellow-500";
    return "text-green-500";
  };

  return (
    <div className="group relative p-3 bg-card border border-border rounded-lg shadow-sm hover:shadow-md transition-shadow">
      <div className="flex justify-between items-start gap-3">
        <p className="text-sm text-foreground flex-1 break-words">{message.content}</p>
        <div className="flex flex-col items-end gap-1 text-xs">
          <span className="text-muted-foreground">
            {new Date(message.timestamp).toLocaleTimeString([], { 
              hour: '2-digit', 
              minute: '2-digit' 
            })}
          </span>
          <span className={`font-mono ${getTimeColor(timeLeft)}`}>
            {formatTime(timeLeft)}
          </span>
        </div>
      </div>
      
      {/* Progress bar showing time remaining */}
      <div className="mt-2 w-full bg-muted rounded-full h-1">
        <div 
          className="bg-primary h-1 rounded-full transition-all duration-1000 ease-linear"
          style={{ 
            width: `${(timeLeft / message.expiresIn) * 100}%` 
          }}
        />
      </div>
    </div>
  );
};

export default MessageItem;
