/**
 * Main chat window component with messages list and controls
 */
import React, { useRef, useEffect } from "react";
import MessageItem from "./MessageItem";

interface Message {
  id: string;
  content: string;
  timestamp: number;
  expiresIn: number;
}

interface ChatWindowProps {
  messages: Message[];
  onPanic: () => void;
  onDeleteMessage: (id: string) => void;
  onToggleStealth: () => void;
  onClearAll: () => void;
}

const ChatWindow: React.FC<ChatWindowProps> = ({ 
  messages, 
  onPanic, 
  onDeleteMessage, 
  onToggleStealth,
  onClearAll 
}) => {
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handlePanicClick = () => {
    if (window.confirm("‚ö†Ô∏è PANIC MODE: This will permanently delete ALL messages. Are you sure?")) {
      onPanic();
    }
  };

  return (
    <div className="flex flex-col h-full bg-background">
      {/* Header */}
      <header className="flex items-center justify-between p-4 border-b border-border bg-card">
        <div className="flex items-center gap-3">
          <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
          <h1 className="text-xl font-bold text-foreground">GhostChat</h1>
          <span className="text-xs text-muted-foreground bg-muted px-2 py-1 rounded">
            E2E Encrypted
          </span>
        </div>
        
        <div className="flex items-center gap-2">
          <button
            onClick={onToggleStealth}
            className="px-3 py-1.5 text-xs bg-secondary text-secondary-foreground rounded hover:bg-secondary/80 transition-colors"
            title="Switch to stealth mode (calculator)"
          >
            Stealth
          </button>
          
          <button
            onClick={onClearAll}
            className="px-3 py-1.5 text-xs bg-muted text-muted-foreground rounded hover:bg-muted/80 transition-colors"
            title="Clear all messages"
          >
            Clear
          </button>
          
          <button
            onClick={handlePanicClick}
            className="px-3 py-1.5 text-xs bg-destructive text-destructive-foreground rounded hover:bg-destructive/90 transition-colors font-medium"
            title="Emergency wipe all data"
          >
            üö® PANIC
          </button>
        </div>
      </header>

      {/* Messages Area */}
      <div className="flex-1 overflow-y-auto p-4 space-y-3">
        {messages && messages.length > 0 ? (
          <>
            {messages.map((message) => (
              <MessageItem 
                key={message.id} 
                message={message} 
                onDelete={onDeleteMessage} 
              />
            ))}
            <div ref={messagesEndRef} />
          </>
        ) : (
          <div className="flex flex-col items-center justify-center h-full text-center space-y-4">
            <div className="w-16 h-16 bg-muted rounded-full flex items-center justify-center">
              <span className="text-2xl">üëª</span>
            </div>
            <div>
              <h3 className="text-lg font-medium text-foreground mb-2">
                Welcome to GhostChat
              </h3>
              <p className="text-sm text-muted-foreground max-w-md">
                Your messages are end-to-end encrypted and automatically self-destruct. 
                Start a conversation below.
              </p>
            </div>
            <div className="text-xs text-muted-foreground space-y-1">
              <p>üîí End-to-end encrypted</p>
              <p>‚è∞ Auto-deleting messages</p>
              <p>üßÆ Stealth calculator mode</p>
              <p>üö® Emergency panic button</p>
            </div>
          </div>
        )}
      </div>

      {/* Status Bar */}
      <div className="px-4 py-2 border-t border-border bg-muted/30">
        <div className="flex items-center justify-between text-xs text-muted-foreground">
          <span>
            {messages.length} message{messages.length !== 1 ? 's' : ''} ‚Ä¢ 
            All encrypted locally
          </span>
          <span>
            {messages.length > 0 && `Oldest expires in ${Math.ceil((Math.min(...messages.map(m => m.expiresIn)) / 1000))}s`}
          </span>
        </div>
      </div>
    </div>
  );
};

export default ChatWindow;
