/**
 * Stealth mode calculator - disguises the chat interface
 */
import React, { useState } from "react";

interface StealthCalculatorProps {
  onExit: () => void;
}

const StealthCalculator: React.FC<StealthCalculatorProps> = ({ onExit }) => {
  const [display, setDisplay] = useState("0");
  const [previousValue, setPreviousValue] = useState<string | null>(null);
  const [operation, setOperation] = useState<string | null>(null);
  const [waitingForOperand, setWaitingForOperand] = useState(false);

  const inputNumber = (num: string) => {
    if (waitingForOperand) {
      setDisplay(num);
      setWaitingForOperand(false);
    } else {
      setDisplay(display === "0" ? num : display + num);
    }
  };

  const inputOperation = (nextOperation: string) => {
    const inputValue = parseFloat(display);

    if (previousValue === null) {
      setPreviousValue(String(inputValue));
    } else if (operation) {
      const currentValue = previousValue || "0";
      const newValue = calculate(currentValue, display, operation);

      setDisplay(String(newValue));
      setPreviousValue(String(newValue));
    }

    setWaitingForOperand(true);
    setOperation(nextOperation);
  };

  const calculate = (firstValue: string, secondValue: string, operation: string) => {
    const prev = parseFloat(firstValue);
    const current = parseFloat(secondValue);

    switch (operation) {
      case "+":
        return prev + current;
      case "-":
        return prev - current;
      case "√ó":
        return prev * current;
      case "√∑":
        return current !== 0 ? prev / current : 0;
      default:
        return current;
    }
  };

  const performCalculation = () => {
    if (previousValue !== null && operation) {
      const newValue = calculate(previousValue, display, operation);
      setDisplay(String(newValue));
      setPreviousValue(null);
      setOperation(null);
      setWaitingForOperand(true);
    }
  };

  const clearAll = () => {
    setDisplay("0");
    setPreviousValue(null);
    setOperation(null);
    setWaitingForOperand(false);
  };

  const clearEntry = () => {
    setDisplay("0");
  };

  // Secret exit sequence: press "0000" to exit stealth mode
  const handleSecretExit = (value: string) => {
    if (display === "000" && value === "0") {
      onExit();
      return;
    }
    inputNumber(value);
  };

  const buttons = [
    { label: "C", action: clearAll, className: "bg-secondary text-secondary-foreground" },
    { label: "CE", action: clearEntry, className: "bg-secondary text-secondary-foreground" },
    { label: "‚å´", action: () => setDisplay(display.slice(0, -1) || "0"), className: "bg-secondary text-secondary-foreground" },
    { label: "√∑", action: () => inputOperation("√∑"), className: "bg-accent text-accent-foreground" },
    
    { label: "7", action: () => inputNumber("7"), className: "bg-card text-card-foreground" },
    { label: "8", action: () => inputNumber("8"), className: "bg-card text-card-foreground" },
    { label: "9", action: () => inputNumber("9"), className: "bg-card text-card-foreground" },
    { label: "√ó", action: () => inputOperation("√ó"), className: "bg-accent text-accent-foreground" },
    
    { label: "4", action: () => inputNumber("4"), className: "bg-card text-card-foreground" },
    { label: "5", action: () => inputNumber("5"), className: "bg-card text-card-foreground" },
    { label: "6", action: () => inputNumber("6"), className: "bg-card text-card-foreground" },
    { label: "-", action: () => inputOperation("-"), className: "bg-accent text-accent-foreground" },
    
    { label: "1", action: () => inputNumber("1"), className: "bg-card text-card-foreground" },
    { label: "2", action: () => inputNumber("2"), className: "bg-card text-card-foreground" },
    { label: "3", action: () => inputNumber("3"), className: "bg-card text-card-foreground" },
    { label: "+", action: () => inputOperation("+"), className: "bg-accent text-accent-foreground" },
    
    { label: "0", action: () => handleSecretExit("0"), className: "bg-card text-card-foreground col-span-2" },
    { label: ".", action: () => inputNumber("."), className: "bg-card text-card-foreground" },
    { label: "=", action: performCalculation, className: "bg-primary text-primary-foreground" },
  ];

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <div className="w-full max-w-sm bg-card border border-border rounded-lg shadow-lg overflow-hidden">
        {/* Calculator Header */}
        <div className="bg-muted p-2 text-center">
          <h1 className="text-sm font-medium text-muted-foreground">Calculator</h1>
        </div>

        {/* Display */}
        <div className="p-4 bg-background border-b border-border">
          <div className="text-right text-2xl font-mono bg-muted p-3 rounded border min-h-[60px] flex items-center justify-end">
            <span className="text-foreground">{display}</span>
          </div>
        </div>

        {/* Button Grid */}
        <div className="p-4">
          <div className="grid grid-cols-4 gap-2">
            {buttons.map((button, index) => (
              <button
                key={index}
                onClick={button.action}
                className={`
                  ${button.className} 
                  ${button.label === "0" ? "col-span-2" : ""} 
                  h-12 rounded border border-border hover:opacity-80 transition-opacity font-medium text-sm
                `}
              >
                {button.label}
              </button>
            ))}
          </div>
          
          {/* Secret hint */}
          <div className="mt-4 text-xs text-center text-muted-foreground">
            Tip: Press 0000 for advanced features
          </div>
        </div>
      </div>
    </div>
  );
};

export default StealthCalculator;
